.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}
.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}
.project-card{
    background: black;
    padding: 1.0rem 1rem;
}
.project-card img{
    width: 100%;
}
.project-title{
    color: white;
    padding: 1rem 0;
}
.pro-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}
.pro-btns {
    display: flex;
    justify-content: space-between;
    padding: 2rem 0;
}

.pro-btns .btn {
    padding: 0.8rem 1.5rem; /* Adjust padding to increase button size */
    border-radius: 20px; /* Increase border radius for rounder corners */
}

@media screen and (max-width: 740px){
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: repeat(2, 1fr);
    }

}